body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.content{
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 1em;
    gap: 1em;
    margin-top: 1em;
    margin-bottom: 1em;
}


.card-container{
    /*display: inline-block;*/
    float: left;
    

    width: 300px;
    overflow: hidden;
    box-shadow:0px 0px 15px -5px;
    transition: 0.3s;
    -webkit-animation: ease-in;
            animation: ease-in;

}

.card-container:hover{
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    box-shadow:0px 0px 15px 0px;     
}


.image-container img{
    overflow: hidden;
    height: 200px;

}

.card-content{
    margin: 1rem;
    margin-top: 0.2rem;
}

h3, p{
    margin: 0;
    padding: 0;
}

.dog-name{
    margin-bottom: 0.5rem;  
}

.btn{
  display: flex;
  justify-content: center;  
}

.btn button {
    padding: 00.5rem;
    background-color: white;
    border: none;
    transition: 0.2s;
    margin-bottom: 0.5rem;
    border-radius: 3px;
}

.btn button:hover{
    background: rgba(27, 156, 252, 0.1);
    -webkit-transform: scale(1.13);
            transform: scale(1.13);    
}

.btn-text{
    text-transform: uppercase;
    color: #1b9cfc;
    text-decoration: none;
    font-weight: bold;
}

.info-title{
    color: rgb(74, 170, 202);
    font-size: 3em;
}
  
.owner-content{
   display: flex;
   flex-direction: column;
   align-items: center;
}

h1 {
    color: rgb(43, 178, 223);
    font-size: 3em;
}

h2 {
    font-size: 3em;
    color: rgb(1, 138, 184);
}

.owner-info{
  
    padding: 1.5em;
    overflow: hidden;
    box-shadow:0px 0px 15px -5px;

}

.owner-name{
    font-size: 2em;
    font-weight: bold;
}

.owner-lastname{
    font-size: 2em;
    font-weight: bold;
    margin-top: 0.5em;
}

.owner-contact{
    font-size: 2em;
    font-weight: bold;
    margin-top: 0.5em;
}

.owner-btn{
    display: flex;
    justify-content: center;  
    margin-top: 1em;
}

.owner-btn button{
    padding: 00.5rem;
    background-color: white;
    border: none;
    transition: 0.2s;
    margin-bottom: 0.5rem;
    border-radius: 3px;
}

.owner-btn button:hover{
    background: rgba(27, 156, 252, 0.1);
    -webkit-transform: scale(1.12);
            transform: scale(1.12);    
}

.owner-btn-text{
    text-transform: uppercase;
    color: #1b9cfc;
    text-decoration: none;
    font-weight: bold;
}

/* p {
    font-size: 2em;
    font-weight: bold;
} */
.primary  {
    color: blue;
    font-size: 75px;
}

.enterButton {
    width: 8em;
    border: 8px solid lightblue;
    background: rgb(53, 117, 138);
    color: white;
    border-radius: 70em;
    font-size: 1.4em;
    margin: auto;

    align-items: center;
    justify-content: center;
}

.enterButton:hover{
    opacity: 0.5;
    transition-duration: 0.5s;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

body {
    height: 100%;
    width: 100%;
}
